export default {
	data() {
		return {}
	},
	onLoad() {
		this.checkToken()
	},
	methods: {
		appLoginqx() {
			uni.switchTab({
				url: '../index/index'
			})
		},
		checkToken() {
				uni.getSetting({
					success(res) {
						console.log("授权：", res);
						if (!res.authSetting['scope.userInfo']) {
							//这里调用授权
							console.log("当前未授权");
						} else {
							//用户已经授权过了
							console.log("当前已授权");
							uni.switchTab({
								url: '../index/index'
							})
						}
					}
				})
			// let token = uni.getStorageSync('auth_token');
			// if (token) {
			// 	uni.switchTab({
			// 		url: '../index/index'
			// 	})
			// }
		},
		appLoginWx() {
			var that = this;
			uni.getProvider({
				service: 'oauth',
				success: function(res) {
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: (res) => {
								// _self.authorization = res.code;
								uni.getUserInfo({
									provider: 'weixin',
									success: (info) => { //这里请求接口
										console.log(info, res);
										let code = res.code;
										let nickname = info.userInfo.nickName
										let avatar = info.userInfo.avatarUrl
										that.$api.auth.login(code, avatar, nickname);
										uni.switchTab({
											url: '../index/index'
										})
									},
									fail: () => {
										uni.showToast({
											title: "微信登录授权失败",
											icon: "none"
										});
									}
								})

							},
							fail: () => {
								uni.showToast({
									title: "微信登录授权失败",
									icon: "none",
								});
							}
						})

					} else {
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon: "none"
						});
					}
				}
			});
		},
	}
}
